html {
  background: #f7f8fa;
  font: 14px/1.5 sans-serif;
  color: #43536d;
  height: 100%;
  padding: 0;
  margin: 0;
}

body {
  border-top: 4px solid #005a93;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  min-height: 100%;
  margin: 0;
}

header h1 {
  font-size: 2rem;
  margin-bottom: 2rem;
}

section {
  min-width: 20rem;
  width: 100%;
  max-width: 34rem;
  padding: 0 2rem;
  box-sizing: border-box;
  flex-grow: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

a {
  color: #0071c4;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

footer {
  padding: 1rem;
  margin-top: 4rem;
  height: 5rem;
  box-sizing: border-box;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid #ced5e1;
  width: 100%;
  overflow: hidden;
}

fieldset {
  border: 0;
  padding: 0;
  margin: 0;
}

.field {
  margin-bottom: 1em;
}

.text input {
  font-size: 1em;
  padding: 0.7em;
}

.middleColumn {
  margin: 0.2em 0;
}

.field.message {
  font-size: 0.8em;
  margin-top: 0.5em;
  display: block;
}

.message.required {
  color: #d40404;
}

.btn-toolbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

input {
  background: white;
  padding: 0.4em 0.5em;
  box-sizing: border-box;
  border: 1px solid #ced5e1;
  border-radius: 2px;
  width: 100%;
}

input[type=checkbox],
input[type=radio] {
  margin-right: 0;
  margin-left: 0;
  width: auto;
  vertical-align: bottom;
}

button,
input[type=button],
input[type=submit] {
  background: #008a00;
  border: 1px solid #008a00;
  border-radius: 3px;
  padding: 0.5rem 1em;
  color: white;
  width: auto;
}

button:hover,
input[type=button]:hover,
input[type=submit]:hover {
  background: darkgreen;
  border-color: #005700;
}

button:active,
input[type=button]:active,
input[type=submit]:active {
  background: #005700;
  border-color: #004a00;
}




